@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
.testimonio {
  background-color: #2e2344;
  color: #FFFFFF;
  padding: 2rem;
  border-radius: 2rem;
}
.testimonio blockquote {
  padding-left: 5rem;
  position: relative;
  line-height: 3.5rem;
}
.testimonio blockquote::before {
  position: absolute;
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  display: block;
  width: 6rem;
  height: 8rem;
  left: -2rem;
}

.entrevistado {
  color: #FFFFFF;
}